@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;
$color-primary: #0C8EFF !default;
$color-primary-dark: #0070D2 !default;
$color-accent: #F88962 !default;
$color-accent-dark: $palette-deep-orange-700 !default;
$color-primary-contrast: $color-dark-contrast !default;
$color-accent-contrast: $color-dark-contrast !default;

@import "../base";

.icon {
  margin-right: $unit;
  font-size: 1.8 * $unit;
}

.root {
  position: relative;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  line-height: 1.5;
  cursor: pointer;
  transition: opacity $animation-duration $animation-curve-default;
  &:not(.active) {
    opacity: .5;
  }
  &:hover, &:active {
    opacity: 1;
  }
  > * {
    vertical-align: middle;
  }
  > abbr {
    text-transform: capitalize;
  }
  > small {
    margin-left: .8 * $unit;
    font-size: $font-size-tiny;
    text-align: center;
  }
}



