@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;
$color-primary: #0C8EFF !default;
$color-primary-dark: #0070D2 !default;
$color-accent: #F88962 !default;
$color-accent-dark: $palette-deep-orange-700 !default;
$color-primary-contrast: $color-dark-contrast !default;
$color-accent-contrast: $color-dark-contrast !default;

@import "../base";
@import "./config";

.horizontal {
  > [data-react-toolbox="button"], > [data-react-toolbox="link"] {
    display: inline-block;
    margin: 0 $navigation-space / 2;
  }
}

.vertical {
  > [data-react-toolbox="button"], > [data-react-toolbox="link"] {
    display: block;
    margin: $navigation-space / 2;
  }
}

.vertical, .horizontal {
  padding: $navigation-space / 2;

  > [data-react-toolbox="link"] {
    color: $navigation-color;
  }
}



